<template>
  <div>
    <div class="back">
      <app-bar
        back-icon
        title="公告栏"
        hideMessage
        hideDemand
        hideUserMenu
      ></app-bar>
      <v-content style="min-height: calc(100vh - 64px)">
        <v-container>
          <v-card class="mx-auto mt-5" min-height="400" max-width="800">
            <v-card-title
              ><v-icon>mdi-card-bulleted-settings-outline</v-icon
              >公告栏</v-card-title
            >
            <v-list>
              <v-list-item
                v-for="item in noticeList"
                :key="item.id"
                style="cursor:pointer"
              >
                <v-list-item-avatar>
                  <v-icon class="yellow darken-3 white--text"
                    >mdi-bullhorn</v-icon
                  >
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title
                    v-text="item.title"
                    @click="handleToDetail(item.id)"
                    class="announce"
                  ></v-list-item-title>
                  <v-list-item-subtitle
                    v-text="item.time"
                  ></v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-action>
                  <v-btn icon>
                    <v-icon color="grey lighten-1">mdi-information</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
            </v-list>
          </v-card>
        </v-container>
      </v-content>
      <AppFooter></AppFooter>
    </div>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'noticeList',
  components: {
    AppBar,
    AppFooter
  },
  data: function() {
    return {
      noticeList: []
    }
  },
  methods: {
    async getNoticeList() {
      let res = await this.$http.get('/api/home/announces')
      this.noticeList = res.data.data.list
    },
    handleToDetail(id) {
      this.$router.push('/noticeList/detail/' + id)
    }
  },
  mounted() {
    this.getNoticeList()
  }
}
</script>

<style scoped>
.announce:hover {
  color: red;
}
.back {
  background-color: #f2f2f2;
}
a {
  text-decoration: none;
  color: #444;
}
.time {
  font-size: 30px;
  line-height: 30px;
  padding-top: 6px;
  padding-bottom: 6px;
  border-right: 1px solid #ccc;
  padding-right: 30px;
  margin-right: 30px;
}
.time {
  float: left;
  font-size: 14px;
  color: #666;
}
</style>
